.about {
  color: #fff;
  background-color: #262626;
  background-image: url("https://www.transparenttextures.com/patterns/inspiration-geometry.png");
  padding: 30px 0;
}

.about_container {
  padding: 30px 0 100px;
  margin: 0 90px;

  h1 {
    padding: 0 0 40px;
  }

  h3 {
    padding: 40px 0 10px 0;
  }

  p {
    font-size: 20px;
  }

  span {
    color: #ffd523;
    font-weight: 600;

    &:hover {
      color: #c25af7;
    }
  }

  button {
    background-color: inherit;
    color: #ffd523;
    border: none;
    width: 140px;
    height: 32px;
    font-size: 17px;
    margin-top: 50px;
    text-align: left;
    font-weight: 600;

    .folder,
    .folder_open {
      font-size: 24px;
      vertical-align: text-bottom;
      color: gray;
    }

    .folder_open {
      color: #ffd523;
      font-size: 26px;
    }
  }
}
