.contact_container {
  text-align: center;

  h1 {
    padding: 50px 0;
  }
}

.contact_item {
  display: flex;
  justify-content: center;
  margin: 50px 0;
}

.social_media {
  margin: 30px;
  background-color: lightgray;
  border-radius: 50%;

  &:hover {
    transition: 0.2s ease-in-out;
    background-color: #000;
    color: #fff;
  }
}

.social_media_item {
  font-size: 28px;
  margin: 20px;
}
