.nav {
  font-weight: 700;
  font-size: 22px;
  padding: 20px 30px;

  p {
    text-shadow: 0.5px 0.5px lightgray, -0.5px 0.5px lightgray,
      0.5px -0.5px lightgray, -0.5px -0.5px lightgray;
  }
}

.nav_left {
  position: fixed;
  top: 10px;
  left: 30px;
}

.nav_right {
  position: fixed;
  top: 10px;
  right: 30px;
}
