/* responsive icons */

.hue_shift {
  animation: hue-shift 5s infinite alternate;
}

.responsive_icon {
  position: absolute;
  font-size: 42px;
}

.fa-code-branch {
  top: 10px;
  left: 190px;
  animation: rotate 2s infinite alternate;
  color: #ffbcbc;
}

.fa-cog {
  top: 120px;
  left: 70px;
  animation: rotate 2.5s infinite alternate;
  color: #7dedff;
}

.fa-star {
  top: 190px;
  right: 50px;
  animation: rotate 2s infinite alternate;
  color: #b3e283;
}

.fa-wrench {
  top: 80px;
  right: 220px;
  animation: move-up 2s infinite alternate;
  color: #d9dd6b;
}

.fa-code {
  top: 20px;
  right: 90px;
  font-size: 50px;
  animation: move-down 2s infinite alternate;
  color: #c490e4;
}

/* animation */
@keyframes move-up {
  from {
    transform: translateY(0);
  }
  to {
    transform: translateY(2rem);
  }
}

@keyframes move-down {
  from {
    transform: translateY(0);
  }
  to {
    transform: translateY(-2rem);
  }
}

@keyframes rotate {
  from {
    transform: rotate(-45deg);
  }
  to {
    transform: rotate(90deg);
  }
}

@keyframes hue-shift {
  from {
    filter: hue-rotate(0);
  }
  to {
    filter: hue-rotate(365deg);
  }
}
